body,
html,
div {
  margin: 0;
  padding: 0;
  border: 0;
  width: 100%;
  background: #0b0e22;
}

/* Search styling */

.search-container {
  display: flex;
  width: 100%;
  margin: 0 auto;
}

.search {
  border: none;
  background: #171821;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding: 0.35em 0.75em;
  border: none;
  font-size: 1.1em;
  text-decoration: none;
  line-height: normal;
  font-family: 'Roboto', sans-serif;
  color: #fcfcec;
  max-height: 3em;
  flex: 2 0;
}

.search:focus {
  background: #21253f;
  transition: background 400ms ease-in-out;
  outline: none;
}

.search-button {
  flex: 0 0;
  max-height: 3em;
  font-size: 1.1em;
  letter-spacing: 0.2em;
  text-transform: uppercase;
  background: #0b1242;
  color: #fcfcec;
  border: none;
  padding: 0.35em 0.75em;
  cursor: pointer;
}

.search-button:hover {
  background: #333f8c;
}

.city-container {
  display: flex;
  flex-direction: column;
}

.city {
  margin: auto;
  font-family: 'Roboto', sans-serif;
  font-size: 3em;
  color: #fcfcec;
}

.errors {
  margin: auto;
  font-family: 'Lato', sans-serif;
  font-size: 1.2em;
  color: #9c3032;
}

/* Weather styling */

.wrapper {
  display: flex;
  justify-content: center;
  background: #0b0e22;
}

.tempature {
  margin-bottom: 0.5em;
  font-family: 'Roboto', sans-serif;
  font-size: 4em;
  color: #fcfcec;
}

.weather-wrapper {
  display: flex;
  justify-content: space-around;
  color: #fcfcec;
  background: #0b0e22;
  font-family: 'Lato', sans-serif;
}

.weather-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-self: center;
  flex-basis: 30%;
  border-radius: 3px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.18);
  background: #24273b;
}

.weather-heading {
  font-size: 1.3em;
}

